<template>
  <v-container>
    <v-row>
      <v-dialog
        v-model="show"
        scrollable
        :persistent="dialog.persistent ? dialog.persistent : true"
        :fullscreen="dialog.fullScreen"
        :max-width="dialog.maxWidth ? dialog.maxWidth : '600px'"
        :retain-focus="false"
      >
        <v-card>
          <v-card-title>
            {{
              dialog.heading ? dialog.heading : this.$t('crud.viewDialog.view')
            }}
          </v-card-title>
          <v-card-text>
            <slot name="view"></slot>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              :color="
                dialog.closeButtonColor ? dialog.closeButtonColor : 'error'
              "
              @click="closeDialog"
              >{{
                dialog.closeButtonText
                  ? dialog.closeButtonText
                  : this.$t('crud.viewDialog.closeButton.text')
              }}</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
export default {
  props: {
    show: { type: Boolean, default: false },
    dialog: { type: [Object, Boolean], default: () => {} }
  },
  data() {
    return {}
  },
  methods: {
    // close dialog
    closeDialog() {
      this.$emit('close')
    }
  }
}
</script>
<style lang="scss"></style>
